

import { AutoSizer, List as VirtualList, ListRowProps } from 'react-virtualized'
import { useState } from 'react';
import styles from './index.less'
import Loading from './Loading';
import classNames from 'classnames';

const List = (props: any) => {

    const {
        list = [],
        render = () => { }, loading,
        empty = <div className={styles.loading}>暂无数据</div>,
        isLoadMore, handleScrollMore,
        className,
    } = props;

    const renderRow = (itemProps: ListRowProps) => {
        const { index, style } = itemProps;

        if (!list[index]) {
            return <div style={style} key={`${index}_footer`} >
                <Loading className={styles.svg} />
            </div>
        }

        return <div style={style} key={list[index].key} >{render(index)}</div>

    }

    const handleScroll = (arg: any) => {
        const { clientHeight, scrollHeight, scrollTop } = arg;
        if (isLoadMore && clientHeight + scrollTop >= scrollHeight) {
            if (handleScrollMore) {
                handleScrollMore()
            }
        }

    }

    const renderContent = () => {
        if (loading) {
            return <div className={styles.loading}><Loading className={styles.svg} /></div>
        }
        if (list.length <= 0) {
            return empty
        }

        return (
            <AutoSizer>
                {
                    ({ width, height }) => (
                        <VirtualList
                            rowCount={isLoadMore ? list.length + 1 : list.length}
                            rowHeight={28}
                            rowRenderer={renderRow}
                            height={height}
                            width={width}
                            onScroll={handleScroll}
                        />
                    )
                }
            </AutoSizer>
        )
    }
    return (
        <div className={classNames(styles.container,className)}>
            {renderContent()}
        </div>
    )
}

export default List;
